/* Example styles for h1, h2, h3, and h4 tags */

.styled-content h1 {
  font-size: 3rem; 
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-weight: bold;
}

.styled-content h2 {
  font-size: 2.5rem;
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
  font-weight: bold;
}

.styled-content h3 {
  font-size: 2rem; 
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.styled-content h4 {
  font-size: 1.5rem; 
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  font-weight: bold;
}

.styled-content p {
  font-size: 1.5rem; 
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

@media (min-width: 420px) {
  .styled-content h1 {
    font-size: 3rem; 
  }

  .styled-content h2 {
    font-size: 2.5rem;
  }

  .styled-content h3 {
    font-size: 2rem;
  }

  .styled-content h4 {
    font-size: 1.5rem;
  }

  .styled-content p {
    font-size: 1.85rem; 
  }

  .styled-content ul {
    font-size: 1.85rem; 
  }

  .styled-content li {
    font-size: 1.85rem; 
  }

  .styled-content ol {
    font-size: 1.85rem; 
  }
}

@media (min-width: 1281px) {
  .styled-content h1 {
    font-size: 2rem;
  }

  .styled-content h2 {
    font-size: 1.75rem;
  }

  .styled-content h3 {
    font-size: 1.5rem;
  }

  .styled-content h4 {
    font-size: 1.25rem;
  }

  .styled-content p {
    font-size: 1rem; 
  }

  .styled-content ul {
    font-size: 1rem; 
  }

  .styled-content li {
    font-size: 1rem; 
  }

  .styled-content ol {
    font-size: 1rem; 
  }
}


