.map-container {
  height: 100%;
  width: 100%;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
  height: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #F1F1F1;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #C1C1C1;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #888787;
}

.dotted-line22 {
  border-left: 12px dotted #06abf5;
  height: 230px;
  flex-grow: 1;
  margin: 5px 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.full-height-line {
  height: 100px; /* Adjust as needed to ensure continuity */
}

.solid-line {
  border-left: 2px solid;
  height: 80px; /* Adjust as needed */
  margin: 5px 0;
}

.waypoint {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 50%;
}

.waypoint-end {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #000;
  margin: 5px 0;
}

/* In your global CSS file */
.custom-drawer-top {
  top: 92px; /* Default for larger screens */
}

@media (max-width: 1023px) {
  .custom-drawer-top {
    top: 160px; /* Top for small screens (xs) */
  }
}

